
'use client';

import { useEffect, useState, useTransition, use } from 'react';
import { useRouter } from 'next/navigation';
import LoadingLink from '@/components/loading-link';
import { getSessionUser, getEventDetailsAction, checkInAction } from '@/lib/actions';
import { getEventStatus } from '@/lib/event-utils';
import type { Event, Participant } from '@/lib/types';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Loader2, AlertTriangle, CheckCircle, LogIn, ArrowLeft, QrCode, UserCheck, CalendarDays, Home } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { format, parseISO } from 'date-fns';
import { zhCN } from 'date-fns/locale';
import { useLoading } from '@/contexts/loading-context';

interface CheckInPageProps {
  params: { id: string };
}

export default function CheckInPage({ params: paramsProp }: CheckInPageProps) {
  const params = use(paramsProp); 
  const eventId = params.id;
  const { startLoading, stopLoading } = useLoading();

  const [event, setEvent] = useState<Event | null>(null);
  const [currentUser, setCurrentUser] = useState<Participant | null>(null);
  const [isLoading, setIsLoading] = useState(true); 
  const [isCheckingIn, startCheckInTransition] = useTransition();
  const { toast } = useToast();
  const router = useRouter();

  useEffect(() => {
    async function fetchData() {
      if (!eventId) {
        setIsLoading(false);
        return;
      }
      setIsLoading(true); 
      startLoading(); 
      try {
        const fetchedEvent = await getEventDetailsAction(eventId);
        if (fetchedEvent) {
          setEvent(fetchedEvent);
          const sessionUser = await getSessionUser();
          if (sessionUser) {
             const participantInfo = fetchedEvent.participants.find(p => p.id === sessionUser.id);
            setCurrentUser(participantInfo || { ...sessionUser, isCheckedIn: false, joinedEventIds: sessionUser.joinedEventIds || [] });
          } else {
            setCurrentUser(null);
          }
        } else {
          setEvent(null);
          toast({ title: '错误', description: '无法加载活动详情。', variant: 'destructive' });
        }
      } catch (error) {
        console.error("获取签到页面数据失败:", error);
        setEvent(null);
        toast({ title: '错误', description: '加载签到信息时发生错误。', variant: 'destructive' });
      } finally {
        setIsLoading(false); 
        stopLoading(); 
      }
    }
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [eventId, toast]); 

  const handleCheckIn = async () => {
    if (!event || !currentUser) return;

    if (!event.participants.some(p => p.id === currentUser.id)) {
        toast({ title: '签到失败', description: '您尚未报名此活动，无法签到。', variant: 'destructive' });
        return;
    }
    startLoading();
    startCheckInTransition(async () => {
      try {
        const result = await checkInAction(event.id);
        if (result.success) {
          toast({ title: '签到成功！', description: result.message });
          setCurrentUser(prevUser => prevUser ? {...prevUser, isCheckedIn: true} : null);
          setEvent(prevEvent => prevEvent ? ({
            ...prevEvent,
            participants: prevEvent.participants.map(p => p.id === currentUser.id ? {...p, isCheckedIn: true} : p)
          }) : null);
        } else {
          toast({ title: '签到失败', description: result.message, variant: 'destructive' });
        }
      } finally {
        stopLoading();
      }
    });
  };
  
  const handleNavigate = (path: string) => {
    startLoading();
    router.push(path);
  };

  const handleBack = () => {
    startLoading();
    router.back();
  }

  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-15rem)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-2">正在加载签到信息...</p>
      </div>
    );
  }

  if (!event) {
    return (
      <div className="text-center">
        <AlertTriangle className="mx-auto h-12 w-12 text-destructive" />
        <AlertTitle className="mt-4 text-2xl font-semibold">活动未找到</AlertTitle>
        <AlertDescription className="mt-2 text-muted-foreground">
          无法加载此活动的签到信息。
        </AlertDescription>
        <Button onClick={() => handleNavigate('/')} className="mt-6">返回首页</Button>
      </div>
    );
  }

  const eventStatus = getEventStatus(event.date);
  const isParticipant = currentUser && event.participants.some(p => p.id === currentUser.id);
  const alreadyCheckedIn = isParticipant && currentUser?.isCheckedIn === true;
  const canPerformCheckIn = eventStatus === 'current' && isParticipant && !alreadyCheckedIn;

  return (
    <div className="max-w-lg mx-auto">
      <Card className="shadow-xl">
        <CardHeader>
          <CardTitle className="text-2xl font-bold flex items-center">
            <QrCode className="mr-3 h-7 w-7 text-primary" />
            活动签到: {event.name}
          </CardTitle>
          <CardDescription>
            日期: {format(parseISO(event.date), 'yyyy年MM月d日 HH:mm', { locale: zhCN })}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {!currentUser && (
            <Alert variant="destructive">
              <LogIn className="h-4 w-4" />
              <AlertTitle>请先登录</AlertTitle>
              <AlertDescription>
                您需要 <LoadingLink href={`/login?redirect=/events/${event.id}/check-in`} className="font-bold hover:underline">登录</LoadingLink> 才能进行签到。
              </AlertDescription>
            </Alert>
          )}

          {currentUser && !isParticipant && (
            <Alert variant="destructive">
              <AlertTriangle className="h-4 w-4" />
              <AlertTitle>未报名活动</AlertTitle>
              <AlertDescription>
                您尚未报名此活动，因此无法签到。请先返回活动详情页加入活动。
              </AlertDescription>
            </Alert>
          )}

          {currentUser && isParticipant && alreadyCheckedIn && (
            <Alert variant="default" className="bg-green-100 dark:bg-green-900/50 border-green-500 dark:border-green-600">
              <UserCheck className="h-5 w-5 text-green-700 dark:text-green-400" />
              <AlertTitle className="text-green-800 dark:text-green-300">您已签到!</AlertTitle>
              <AlertDescription className="text-green-700 dark:text-green-400">
                您已成功为此活动签到。
              </AlertDescription>
            </Alert>
          )}

          {currentUser && isParticipant && !alreadyCheckedIn && eventStatus !== 'current' && (
            <Alert>
              <CalendarDays className="h-4 w-4" />
              <AlertTitle>签到尚未开始或已结束</AlertTitle>
              <AlertDescription>
                当前无法进行此活动的签到。
              </AlertDescription>
            </Alert>
          )}

          {canPerformCheckIn && (
            <div className="space-y-4">
              <div className="my-4 p-6 border-dashed border-2 border-muted-foreground/50 rounded-md text-center text-muted-foreground">
                <p className="text-sm">模拟人机验证区域</p>
                <p className="text-xs">(Geetest / reCAPTCHA)</p>
              </div>
              <Button onClick={handleCheckIn} disabled={isCheckingIn} className="w-full bg-accent text-accent-foreground hover:bg-accent/90 text-lg py-6">
                {isCheckingIn ? <Loader2 className="mr-2 h-5 w-5 animate-spin" /> : <CheckCircle className="mr-2 h-5 w-5" />}
                确认签到
              </Button>
            </div>
          )}
        </CardContent>
        <CardFooter className="flex flex-col sm:flex-row gap-4 pt-6 border-t">
            <Button variant="outline" onClick={() => handleNavigate(`/events/${event.id}`)} className="w-full sm:w-auto">
                <Home className="mr-2 h-4 w-4" /> 返回活动详情
            </Button>
            <Button variant="ghost" onClick={handleBack} className="w-full sm:w-auto sm:ml-auto">
                <ArrowLeft className="mr-2 h-4 w-4" /> 返回上一页
            </Button>
        </CardFooter>
      </Card>
    </div>
  );
}
